export default {
    components: {
        CycleNav,
        CycleMonitoringCycleInfo,
        CycleMonitoringDetailForm,
        CycleMonitoringDiagnosisForm,
        CycleMonitoringOtherDrugsForm,
        CycleMonitoringTherapyFrom,
        CycleMonitoringLineTable,
        CycleMonitoringComments,
        PatientDetailForm,
        OocyteProviderForm
    },
    setup() {
        const store = useStore();
        const service = inject("cycleMonitoringService");

        const states = ref([
            { label: "Opt 1", value: "opt-1" },
            { label: "Opt 2", value: "opt-2" },
            { label: "Opt 3", value: "opt-3" }
        ]);

        const test = ref();
        const check = ref(false);
        const multiCheck = ref([]);
        const table = service.getTable();
        const comments = service.getComments();

        onMounted(() => {
            if (store.state.artCycle.selectedCycle) {
                store.dispatch(
                    "cyccleMonitoring/getCycleMonitoringDetails",
                    // 2000252776
                    store.state.artCycle.selectedCycle.cycleId
                );
            }
        });

        return {
            states,
            test,
            check,
            multiCheck,
            table,
            comments
        };
    }
};
