<template>
    <p-data-table
        :value="comments"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
    >
        <p-column header="Date" field="signdate" style="width: 120px">
            <template #body="{data}">
                <p-calendar v-model="data.signdate" :showIcon="true" />
            </template>
        </p-column>
        <p-column header="Comment" field="comment">
            <template #body="{ data }">
                <p-textarea
                    v-model="data.comment"
                    rows="3"
                    style="width: 100%"
                />
            </template>
        </p-column>
    </p-data-table>
</template>

<script>
import { ref, computed, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cyccleMonitoring.details
        );

        const comments = ref([]);

        watch(monitorDetails, () => {
            comments.value = store.state.cyccleMonitoring.comments
        })
        return { comments };
    }
};
</script>
